import './nav.css';
import m from './light.png';
import { BrowserRouter as Router,Routes, Route, Link } from 'react-router-dom';

function Nav ({switchM,colorChangeAbout,colorChangeBlogs,colorChangeLogo, colorChangeProjects,colorChangeContact}){

            colorChangeAbout=()=>{
              document.getElementsByClassName("a")[0].style.borderBottom = "4px solid red";
              // document.getElementsByClassName("a")[0].style.borderBottomColor = "blueviolet";
              // document.getElementsByClassName("a")[0].style.borderBottomThickness = "5px";
              document.getElementsByClassName("bl")[0].style.borderBottom = "none";
              document.getElementsByClassName("p")[0].style.borderBottom = "none";
              document.getElementsByClassName("co")[0].style.borderBottom = "none";
            }
            colorChangeBlogs=()=>{
              document.getElementsByClassName("a")[0].style.borderBottom = "none";
              document.getElementsByClassName("bl")[0].style.borderBottom = "4px solid blueviolet";
              // document.getElementsByClassName("bl")[0].style.borderBottomColor = "red";
              // document.getElementsByClassName("bl")[0].style.borderBottomThickness = "5px";
              document.getElementsByClassName("p")[0].style.borderBottom = "none";
              document.getElementsByClassName("co")[0].style.borderBottom = "none";
            }
            colorChangeProjects=()=>{
              document.getElementsByClassName("a")[0].style.borderBottom = "none";
              document.getElementsByClassName("p")[0].style.borderBottom = "4px solid green";
              // document.getElementsByClassName("p")[0].style.borderBottomColor = "green";
              // document.getElementsByClassName("p")[0].style.borderBottomThickness = "5px";
              
              document.getElementsByClassName("bl")[0].style.borderBottom = "none";
              document.getElementsByClassName("co")[0].style.borderBottom = "none";
            }
            colorChangeContact=()=>{
              document.getElementsByClassName("a")[0].style.borderBottom = "none";
              // document.getElementsByClassName("bl")[0].style.borderBottomColor = "blueviolet";
              document.getElementsByClassName("bl")[0].style.borderBottom = "none";
              document.getElementsByClassName("p")[0].style.borderBottom = "none";
              document.getElementsByClassName("co")[0].style.borderBottom = "4px solid slateblue";
              // document.getElementsByClassName("co")[0].style.borderBottomColor = "slateblue";
              // document.getElementsByClassName("co")[0].style.borderBottomThickness = "5px";
            }
            colorChangeLogo =()=>{
              document.getElementsByClassName("a")[0].style.borderBottom = "none";
              
              document.getElementsByClassName("bl")[0].style.borderBottom = "none";
              document.getElementsByClassName("p")[0].style.borderBottom = "none";
              document.getElementsByClassName("co")[0].style.borderBottom = "none";
             
            }
    return(
        <div className='nav'>
              <div className="logoSection">
              <Link style={{ textDecoration: 'none'}} to="/"><h1 className="logoText" onClick={colorChangeLogo} >Sahil Kalkal</h1></Link> {/* Logo */}
                <div className="modeImgs">
                <img className='mode' id="l" onClick={switchM} src={m} />
          

                </div>
               
              </div>
              
              <div className='menu'>
             
              <Link style={{ textDecoration: 'none' }} to="/about"><p className='menuItem'><a  className='link a' onClick={colorChangeAbout} >About</a></p></Link>
              <Link style={{ textDecoration: 'none' }} to="/blogs"><p className='menuItem'><a  className='link bl' onClick={colorChangeBlogs} >Blogs</a></p></Link>
              <Link style={{ textDecoration: 'none' }} to="/projects"><p className='menuItem'><a  className='link p' onClick={colorChangeProjects} >Projects</a></p></Link>
              <Link style={{ textDecoration: 'none' }} to="/contact"><p className='menuItem'><a  className='link co' onClick={colorChangeContact} >Contact</a></p></Link>
              
              <Link style={{ textDecoration: 'none' }} to="/gallery"> <p className='menuItem'><p className='link' ></p></p></Link>
                
                  
              </div>
              
            </div>
    );
}

export default Nav;