import './rnav.css';

import direct from './direct.png'
import React from 'react';
import CodeEditor from '@uiw/react-textarea-code-editor';

function Rnav(){const [code1, setCode1] = React.useState(
`<div class="nav">
    <div class="logo">
        Logo
    </div>
    <div class="menu">
        <div class="menuItem" id="m"  >Item</div>
        <div class="menuItem" id="m" >Item</div>
        <div class="menuItem" id="m" >Item</div>
        <div class="menuItem" id="m" >Item</div>
        <img class="menuItem" onclick="openNav()" id="menuImg" src="menu.png" ></img>
    </div>
</div>`
      );
const [code2, setCode2] = React.useState(
`<div id="myNav" class="overlay">
    <a href="javascript:void(0)" class="closebtn" onclick="closeNav()">&times;</a>
    <div class="overlay-content">
    <a onclick="closeNav()">Item</a>
    <a onclick="closeNav()">Item</a>
    <a onclick="closeNav()">Item</a>
    <a onclick="closeNav()">Item</a>      
    </div>
</div>`
       );
const [code3, setCode3] = React.useState(
`.nav{
    display: flex;
    justify-content: space-between;
}
.logo{
    font-size: 40px;
    padding-left: 5vw;
}
.menuItem{
    font-size: 20px;
}
.menu{
    display: flex;
    column-gap: .5cm;
    align-items: center;
    padding-right: 5vw;
}

#menuImg{
    height: 0.6cm;
    display: none;
}`
       );
const [code4, setCode4] = React.useState(
`.overlay {
    height: 100%;
    width: 0%;
    position: fixed;
    z-index: 1;
    top: 0;
    right: 0;
    background-color: blanchedalmond;
    overflow-y: hidden;
    transition: 0.5s;
  }
  
  .overlay-content {
    position: relative;
    top: 15%;
    width: 100%;
    text-align: center;
    margin-top: 30px;
  }
  p{
    padding-left: 5vw;
    padding-right: 5vw;
  }
 
  
  .overlay a {
    padding: 8px;
    text-decoration: none;
    font-size: 36px;
    color: #28282b;
    display: block;
    transition: 0.3s;
  }
  
  .overlay a:hover, .overlay a:focus {
    color: #f1f1f1;
  }
  
  .overlay .closebtn {
    position: absolute;
    top: 10px;
    right: 45px;
    font-size: 60px;
  }
`
       );
const [code5, setCode5] = React.useState(
`@media screen and (max-width: 900px){
  
    .overlay a{
      font-size: 30px;
    }
}

@media screen and (max-width:500px){
    #m{
        display: none;
    }
    #menuImg{
        display: block;
    }
    .overlay a{
        font-size: 20px;
      }
}`
       );
const [code6, setCode6] = React.useState(
`var x = document.getElementById("myNav")`
       );

const [code7, setCode7] = React.useState(
`openNav=()=>{
    var width = window.innerWidth;
    
    if(width <=900 && width > 800)
    {
      x.style.width = "35%";
    }
    else if(width <=800 && width > 700)
    {
      x.style.width = "40%";
    }
    else if(width <=700 && width > 600)
    {
      x.style.width = "45%";
    }
    else if(width <=600 && width >500)
    {
      x.style.width = "50%";
    }
    else if(width <=500 && width> 400)
    {
      x.style.width = "62%";
    }
    else if(width <=400 && width > 200)
    {
      x.style.width = "75%";
    }
    else if(width <=200 && width > 0)
    {
      x.style.width = "100%";
    }
  }`
       );
const [code8, setCode8] = React.useState(
`closeNav=()=>{
    x.style.width = "0%";
  }`
       );
    return (
        <div className="Rnav">
            <h1 className='latestBlogsHome'>Responsive Nav Bar</h1>
            <p className='pDate' >Published on 17 Feb 2023</p>
            <p className='aboutTextPara' >

                    Welcom to my first blog, as we all know that once in the career of the web development,
                    We all go through this phase when we sit to design a website and we feel restless when we design navigation bar because
                    as a fresher, it's really difficult to make a navigation bar responsive.
                    So, in this blog you will learn to make a good looking responsive navigation bar.<br/>
                    Lets start!!<br/><br/>
                    
                <div className='projectName'>Idea:-</div>
                <p className='aboutTextPara'>
                    First of all, we will build a navigationbar for a laptop's dimension(1440*742).
                    If you are using react.js then please create a react app and make a js file and then start writing the code
                    So, first we will make a parent division and two children divisions and then in the parent division's css file,
                    we will add a property "display : flex" then the children divisions will be in a row.
                    First child division will be for the logo and thebrand name, for that you can make two children divisions in firt child divison too.
                    After that  we will make different divisions for menu items and apply the same "display:flex" property to second child division,
                    then all the menu items will be in a row. 
                    You can add property "column-gap" accordingly
                    We will take a image of three bars and set its display property to none for the dimensions like laptop, tablet.
                    And when it will come to mobile phone then we will set display property to none of all menu items except image and set display property of image from none to flex.
                    At this point we have created 60% of the navigation bar. Now we will have menu items in big displays and image of three bars in small displays.
                    
                    Now, we will make a overlay display which will be over all the elements and initially we will set its height to 0% and you can set width of the overlay display according to your needs.<br/>
                    When user will click on the three bars then the height of the overlay display will be from 0% to your desired height, for example from 0% to 100% then it  will show on screen, and in this we will display our menu items.<br/>
                    <br/>
                    Now lets write code.
        
                </p>
                <div className='projectName'>Code:-</div>
                <p className='aboutTextPara'>
                    Division with classname "nav" is the parent division and divisions with classNames "logo" and "menu" are two children divisions.👇<br/><br/>
                    <CodeEditor
                        value={code1}
                        language="html"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode1(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    Division with class "overlay" is the overlay division.👇
                    <br/><br/>
                    <CodeEditor
                        value={code2}
                        language="html"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode2(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    <b>This all was the HTML part, now comes the CSS part.</b>

                    <br/><br/>
                    CSS part of the classes"nav", "menu", "logo", "menuItem" and a id "menuImg"👇
                    <br/><br/> 
                    <CodeEditor
                        value={code3}
                        data-color-mode="dark"
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode3(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    CSS part of the overlay division.👇
                    <br/><br/>
                    <CodeEditor
                        value={code4}
                        data-color-mode="dark"
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode4(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    CSS for different dimensions👇
                    <br/><br/>
                    <CodeEditor
                        value={code5}
                        data-color-mode="dark"
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode5(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    You can write css for more different dimensions accofing to your need.
                    <br/><br/>
                    <b>This was the CSS part, now comes the JS part.</b>
                    <br/><br/>
                    Select the overlay division using DOM for opening and closing of navigation bar.
                    <br/><br/>
                    <CodeEditor
                        value={code6}
                        data-color-mode="dark"
                        language="js"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode6(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    /> 
                    <br/><br/>
                    openNav() function [It's according to my needs, you can customize it]👇
                    <br/><br/>
                    <CodeEditor
                        value={code7}
                        data-color-mode="dark"
                        language="js"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode7(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    /> 
                    <br/><br/>
                    closeNav() 👇
                    <br/><br/>
                    <CodeEditor
                        value={code8}
                        data-color-mode="dark"
                        language="js"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode8(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    /> 
                    <br/><br/>
                    <b>Code Section Ends Here</b><br/>
                </p><br/>
                <div className='favButtons'>
                        <div id="favButtonInd" >
                        <a className="linkFav" href="https://sahilkalkal.github.io/responsive-nav" target="blank" >Demo </a>
                            <img className='directH'  src={direct} ></img>
                            
                        </div>
                        <div id="favButtonInd" >
                        <a className="linkFav" href="https://github.com/sahilkalkal/responsive-nav" target="blank" >Code </a>
                            <img className='directH'  src={direct} ></img>
                            
                        </div>
                    </div><br/>
                <p className='aboutTextPara' >
                    It was the whole idea and code of the responsive navigation bar. I hope you understood the concept of navigation bar.<br/>
                    Your dedication is appreciable, you reached here 👍<br/>
                    Keep learning!!<br/><br/>
                    If you like the blog, follow me on GitHub and just give a star to this repo at Github.
                </p>
                <p className='aboutTextPara' >
                    You can buy me a coffee too, just click on coffee button ✌️
                </p>
                

            </p>
        </div>
    );
}

export default Rnav;