import './flexbox.css';
import React from 'react';
import CodeEditor from '@uiw/react-textarea-code-editor';
import one from './01.png'
import two from './02.png'
import three from './03.png'
import four from './04.png'
import fd from './flex-direction.svg'
import fw from './flex-wrap.svg'
import jc from './justify-content.svg'



function Flexbox(){
    const [code1, setCode1] = React.useState(
        `
<!-- HTML Code -->
<div className="menu">
    <div className="menuItem">
        Item1
    </div>
    <div className="menuItem">
        Item1
    </div>
    <div className="menuItem">
        Item1
    </div>
</div>
        `
        );

        const [code2, setCode2] = React.useState(
            `
/* CSS Code */
.menu{
    display : flex;
}
    
            `
            );
            const [code3, setCode3] = React.useState(
                `
/* CSS Code */
.menu{
    flex-direction: row | column | row-reverse | column-reverse;
}
        
                `
                );

                const [code4, setCode4] = React.useState(
                    `
/* CSS Code */
.menu{
    flex-wrap : wrap | nowrap | wrap-reverse;
}
            
                    `
                    );
                    const [code5, setCode5] = React.useState(
                        `
/* CSS Code */
.menu{
    flex-flow : column wrap;
}
                
                        `
                        );
    


    return(
        <div className='Flexbox'>
            <h1 className='latestBlogsHome'>CSS Flexbox</h1>
            <p className='pDate' >Published on 20 Feb 2023</p>
            <p className="aboutTextPara">
                Helloo, welcome back to my blogs!!
                <br/>
                Just relax and sit back, in this blog you will learn each and everything about css flexbox. I know many students get confused between flexbox and grid. Don't be panic, I will clear each and everything in this blog. It will be longer than a normal blog but it is filled with quality content.
                <br/>
                Let's start today's blog.
                <br/>
                <br/>
                In simple words, <b style={{color : "blueviolet"}} >flexbox is a way to represent children elements either in a row or in a column.</b> There are several ways of representation like "row", "column", "row-reverse" and "column-reverse", we will talk about them later in this blog.<br/>
                First of all if you want to display elements in flex then you have add a property "display" and set its value to "flex" and then the children elements will be displayed in a row.
                    <br/><br/>
                    Let's see an example, we are taking a parent division/element with class "menu" and children divisions/elements with class "menuItem".
                <br/><br/>
                <CodeEditor
                        value={code1}
                        language="html"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode1(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                />
                    <br/><br/>

                    <CodeEditor
                        value={code2}
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode2(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    <b>Output :-</b>
                    <br/><br/>
                    <img src={one}/>
                    <br/><br/>
                    Now as we applied only "display:flex" to the parent element (div with class "menu"), children elements are displayed in row.<br/><br/>
                    <b><mark>There are different properties for parent element and children elements.<br/>
                        First of all we will discuss about parent element's properties :-</mark>
                    </b>
                   
                    <br/><br/>
                    <h2><u><b>1. flex-direction</b></u> :-</h2><br/>
                    <img className='propImg' src={fd}  />
                    <br/><br/>
                    This property is used to specify the direction of the representation of children elements like "row", "column", "row-reverse" or "column-reverse". Let's see every property one by one.
                    <br/><br/>
                    <CodeEditor
                        value={code3}
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode3(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/>
                    <b> row :-</b> Display all elements in a row from left to right;
                    
                    <br/><br/>
                    <b>column :-</b> Display all elements in a column(first element on top)
                    <br/><br/>
                   
                   <b>row-reverse :-</b> Display all elements in a row from right to left.
                   <br/>
                   Menu items are displayed in the upper right corner of the window.
                  
                   <br/><br/>
                   <b>column-reverse :-</b> Display all elementsin a column(last element on top)
                   
                   <br/><br/>
                   <b>It was flex-direction property, let's move to next.</b>
                   <br/><br/>
                   <h2><u><b>2. flex-wrap</b></u> :-</h2><br/>
                   <img className='propImg' src={fw} />
                   <br/><br/>
                   <CodeEditor
                        value={code4}
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode4(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/>
                   This property tries to fit all the children elements in a single line/row and when it fails to fit all children elements in a single row then it breaks the extra elements and move them to the next row.
                   <br/>
                   There are three values of this property "flex-wrap".<br/><br/>
                   <b>nowrap(default) :-</b> All elements will be in one line.<br/><br/>
                   <b>wrap :-</b> children elements will wrap into multiple lins, from top to bottom<b/><br/><br/>
                   <b>wrap-reverse :-</b> All children elements will wrap into multiple lines, from bottom to top.

                   <br/><br/>
                   <b>It was flex-wrap property, let's move to next.</b>
                   <br/><br/>
                   <h2><u><b>3. flex-flow</b></u> :-</h2>
                   This property is the shorthand or combination of the "flex-direction" and "flex-wrap."<br/>
                   default value is "row nowrap".<br/><br/>
                   <CodeEditor
                        value={code5}
                        language="css"
                        placeholder="Please enter code."
                        onChange={(evn) => setCode5(evn.target.value)}
                        padding={15}
                        style={{
                            fontSize: 15,
                            backgroundColor: "#28282b",
                            color : "whitesmoke",
                            fontFamily: 'ui-monospace,SFMono-Regular,SF Mono,Consolas,Liberation Mono,Menlo,monospace',
                        }}
                    />
                    <br/><br/>
                    <h2><u><b>4. justify-content</b></u> :-</h2>
                    <img className='propImg' src={jc} /><br/><br/>
                    This property is used to set the alignment and position of the children elements horizontally.<br/><br/>
                    <b>flex-start :-</b>The alignment of all the children elements will be left horizontally.
                    <br/><br/>
                    <b>flex-end :-</b>The alignment of all the children elements will be right horizontally.
                    <br/><br/>
                    <b>center :-</b>The alignment of all the children elements will be center horizontally.
                    <br/><br/>


            </p>
        </div>
    );
}

export default Flexbox;