import "./about.css";
import { CircularProgressbar, buildStyles } from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
import me from './me.jpeg'

function About(){
    return (
      <div>
          <div id="about">
                <h1 className='aboutHeadText'>About</h1>
                
                  <div className='photos'>
                  <img src={me} className="aboutImg" />
                 
                  </div>
                  
                  <div className="aboutTextPara">
                      <p className="tagLine"> Hello, I am Sahil Kalkal.</p>
                      I am a Front End Web Developer<br/>
                      I completed my Matric degree from Dharam Deep Secondary Public School in 2018.<br/>
                      I completed my Higher Secondary degree from Shri Daulat Ram Senior Secondary Public School in 2020.<br/>
                      Currently I am pursuing my Bachelor's degree in Computer Science Engineering from Maharshi Dayanand University.<br/>
                      I love to design templates and I use react.js for web development.<br/>
                      You can see my projects in Projects section.<br/>
                      Thank You❤️

                      
                </div>
         </div>
         <div id="skills">
                <h1 className='aboutHeadText'>Skills</h1>
                <div className='aboutText'>
                  <div className="aboutTextPara" id='skillsCollection'>
                      <br/>
                      <div className="skillssss">
                        <div className='skill'>
                          <b className="skillName">HTML5</b>
                          <div className='skillCircle'>
                            <CircularProgressbar
                                 value={90}
                                 text={"90%"}
                                 styles={buildStyles({
                                      pathTransitionDuration: 2,
                                      pathColor: "blueviolet",
                                      textColor: 'blueviolet',
                                      
                                    })}
                                  />
                          </div>
                        </div>
                        
                        <div className='skill'>
                          <b className="skillName">CSS3</b>
                          <div className='skillCircle' >
                            <CircularProgressbar
                                  
                                 value={70}
                                 text={"70%"}
                                 styles={buildStyles({
                                      pathTransitionDuration: 2,
                                      pathColor: "blueviolet",
                                      textColor: 'blueviolet',
                                      
                                    })}
                                  />
                          </div>
                        </div>
                        
                        <div className='skill'>
                          <b className="skillName">JS</b>
                          <div className='skillCircle' >
                            <CircularProgressbar
                                 value={65}
                                 text={"65%"}
                                 styles={buildStyles({
                                      pathTransitionDuration: 2,
                                      pathColor: "blueviolet",
                                      textColor: 'blueviolet',
                                      
                                    })}
                                  />
                          </div>
                        </div>
                        
                        <div className='skill'>
                          <b className="skillName">React.js</b>
                          <div className='skillCircle'>
                            <CircularProgressbar
                                 value={60}
                                 text={"60%"}
                                 styles={buildStyles({
                                      pathTransitionDuration: 2,
                                      pathColor: "blueviolet",
                                      textColor: 'blueviolet',
                                      
                                    })}
                                  />
                          </div>
                        </div>
                        
                        <div className='skill'>
                          <b className="skillName">C++</b>
                          <div className='skillCircle'>
                            <CircularProgressbar
                                 value={80}
                                 text={"80%"}
                                 styles={buildStyles({
                                      pathTransitionDuration: 2,
                                      pathColor: "blueviolet",
                                      textColor: 'blueviolet',
                                      
                                    })}
                                  />
                          </div>
                        </div>


                        
                        
                        
                      </div>
                  </div>
                </div>
              </div>
      </div>
        
    );
}

export default About;